import React from "react";
import { NavLink } from 'react-router-dom';
import { translate } from './dictionary'

import '../css/style.scss';

import Loading from './Loading';

import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHome, faFilm, faGlobe, faDna, faFighterJet, faSnowplow, faStreetView } from '@fortawesome/free-solid-svg-icons';
import { faRedditAlien} from '@fortawesome/free-brands-svg-icons';
library.add(faHome, faFilm, faGlobe, faDna, faRedditAlien, faFighterJet, faSnowplow, faStreetView);

const Menu = props => {
    const { loaded, menuPositions } = props;
    const menu = menuPositions.map( (category,index) => (
        <li 
            key={index}
        >
            <NavLink 
                to={category.path} 
                exact={category.exact}
            >
                <div className="icon">
                    {category.icon === '' ? '' : <FontAwesomeIcon icon={category.icon} />}
                    <span>{translate(category.name)}</span>
                </div>
            </NavLink>
        </li>
    ));

    return(
        <>
            <Loading 
                loaded = {loaded}
            />
            <div className="mainMenuDiv">
                <nav className="mainMenu">
                    <ul className="">
                        {menu}
                    </ul>
                </nav>
            </div>
        </>
    )
}

export default Menu