import React, { useState } from "react";  
import {Route, NavLink} from 'react-router-dom'

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { translate } from './dictionary'

import '../css/style.scss';

const ListProperties = props => {

    const [inputSearch, setInputSearch] = useState("");
    const { menuPositions } = props;
    const { films, people, planets, species, starships, vehicles } = props;

    const sortObjects = key => {
        return function innerSort(a, b) {
          if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key))
            return 0;
    
          const varA = (typeof a[key] === 'string')
            ? a[key].toUpperCase() : a[key];
          const varB = (typeof b[key] === 'string')
            ? b[key].toUpperCase() : b[key];
    
          let comparison = 0;
          if (varA > varB)
            comparison = 1;
          else if (varA < varB) 
            comparison = -1;
          return comparison
        };
    }

    function buildList(array, path, filter) {
        let arrayReturn = "";
        if (array !== undefined && path !== undefined && array.length > 0) {
            let arrayTemp = array;
            const key = path.substring(1) === "films" ? "title" : "name"
            
            arrayTemp = arrayTemp.sort(sortObjects(key));
 
            if (filter.length > 2) {
                arrayReturn = arrayTemp.filter( element => (
                    element[key].toUpperCase().includes(filter.toUpperCase()) || translate(element[key]).toUpperCase().includes(filter.toUpperCase())
                ))
                arrayReturn = arrayReturn.map( (element, index) => (
                    <li key={index}> 
                        <NavLink to={`${path}/${element[key]}`}>
                            {translate(element[key])}
                        </NavLink>                      
                    </li>)
                )
            }
            else {
                arrayReturn = array.map( (element, index) => (                    
                    <ListItem key={index}>
                        <ListItemText>
                            <NavLink to={`${path}/${element[key]}`}>
                                {translate(element[key])}
                            </NavLink>   
                        </ListItemText>
                    </ListItem>
                ));
            }
        }
        arrayReturn = <List>{arrayReturn}</List>
        return arrayReturn;
    }   

    const inputSearchChange = e => {
        setInputSearch(e.target.value)
    }

    const Input = () => {
        return (
            <input
                ref={(input) => {
                    if (input)
                        input.focus();
                }}
                type = "text"
                className = "search"
                placeholder = "Wyszukaj... (min. 3 znaki)"
                onChange = {inputSearchChange.bind(this)}
                value = {inputSearch}
            />
        )
    }

    return (
        <>
            <Route path="/films" component={() => {
                return (
                    <>
                        <Input />
                        <div className="selectedList">
                            {buildList(films,menuPositions.filter(el => el.name === "films")[0].path,inputSearch)}
                        </div>
                    </>
                )
            }} />
            <Route path="/people" component={() => {
                return (
                    <>
                        <Input />
                        <div className="selectedList">
                            {buildList(people,menuPositions.filter(el => el.name === "people")[0].path,inputSearch)}
                        </div>
                    </>
                )
            }} />
            <Route path="/planets" component={() => {
                return (
                    <>
                        <Input />
                        <div className="selectedList">
                            {buildList(planets,menuPositions.filter(el => el.name === "planets")[0].path,inputSearch)}
                        </div>
                    </>
                )
            }} />
            <Route path="/species" component={() => {
                return (
                    <>
                        <Input />
                        <div className="selectedList">
                            {buildList(species,menuPositions.filter(el => el.name === "species")[0].path,inputSearch)}
                        </div>
                    </>
                )
            }} />
            <Route path="/starships" component={() => {
                return (
                    <>
                        <Input />
                        <div className="selectedList">
                            {buildList(starships,menuPositions.filter(el => el.name === "starships")[0].path,inputSearch)}
                        </div>
                    </>
                )
            }} />
            <Route path="/vehicles" component={() => {
                return (
                    <>
                        <Input />
                        <div className="selectedList">
                            {buildList(vehicles,menuPositions.filter(el => el.name === "vehicles")[0].path,inputSearch)}
                        </div>
                    </>
                )
            }} />
        </>
    )
}

export default ListProperties

